<template>
	<div class="lizi">
		<header>JS小例子</header>
		<span class="titlea">右滑列表查看更多</span>
		<cube-scroll
          ref="scroll"
          direction="horizontal"
         >
			<div class="component list-wrapper">
				<button class="list-item active" @click="cpe = 'csz'">猜数字游戏</button>
				<button class="list-item" @click="cpe = 'tk'">图库</button>
				<button class="list-item" @click="cpe = 'sz'">时钟</button>
				<button class="list-item" @click="cpe = 'dtgbtp'">自定义照片样式</button>
				<button class="list-item" @click="cpe = 'bgtx'">收缩背景</button>
				<button class="list-item" @click="cpe = 'ssglsc'">搜索诗词</button>
				<button class="list-item" @click="cpe = 'hh'">画画</button>
				<button class="list-item" @click="cpe = 'avideo'">视频播放</button>
				<button class="list-item" @click="cpe = 'scrollimg'">滑动显图片</button>
				<button class="list-item" @click="cpe = 'lbt'">轮播图</button>
				<button class="list-item" @click="cpe = 'yddlbt'">移动端轮播图</button>
				<button class="list-item" @click="cpe = 'ljz'">懒加载</button>
				<button class="list-item" @click="cpe = 'lhj'">预加载</button>
				<span id="animate"></span>
			</div>
		</cube-scroll>
		
		<div class="component-box">
			<component :is="cpe"></component>
		</div>
	</div>
</template>

<script>
	import $ from 'jquery'
	export default{
		data(){
			return{
				cpe:'lhj'
			}
		},
		components:{
			'csz':() => import('../components/lizi/csz.vue'),
			'tk':() => import('../components/lizi/tk.vue'),
			'sz':() => import('../components/lizi/sz.vue'),
			'dtgbtp':() => import('../components/lizi/dtgbtp.vue'),
			'bgtx':() => import('../components/lizi/bgtx.vue'),
			'ssglsc':() => import('../components/lizi/ssglsc.vue'),
			'hh':() => import('../components/lizi/hh.vue'),
			'avideo':() => import('../components/lizi/video.vue'),
			'scrollimg':() => import('../components/lizi/scrollimg.vue'),
			'lbt':() => import('../components/lizi/lbt.vue'),
			'yddlbt':() => import('../components/lizi/yddlbt.vue'),
			'ljz':() => import('../components/lizi/ljz.vue'),
			'lhj':() => import('../components/lizi/lhj.vue'),
		},
		created(){

		      $(function animate() {
		      	$('#animate').width($('.list-item').innerWidth());
		        $('.list-item').click(function(){
			        $('#animate').animate({
			           left : $(this).position().left + 'px',
			           width : $(this).innerWidth() + 'px',
			         },300);

			      	$(this).addClass("active").siblings().removeClass("active");

		     	})
		   	 })
	    }
	}
</script>

<style lang="scss">
@import "../assets/css/scss_theme.scss";

	.lizi{
		header{
			height: 40px;
			background:$default-bgColor;
			line-height: 50px;
			color:$default-color;
			padding-left: .4rem;
			font-size: 20px;
			display: inline-block;
		}
		.titlea{
			margin-left: .4rem;
			font-size: .2rem;
			color: rgba(0,0,0,.6);
		}
		.component{
			height: .8rem;
			background: $default-bgColor;
			line-height: .8rem;
			font-size: 16px;
			color:$default-color;
			padding: 0 .3rem;
			box-sizing:border-box;
			position:relative;
			background:rgba(0,0,0,.05);
			button{
				background:transparent;
				margin-right: .3rem;
				padding: 0 .1rem;
			}
			#animate{
				left: .3rem;
				bottom:0;
				width: 3px;
				position:absolute;
				height: .08rem;
				background:#4a4c5b;
				z-index: 99;
			}
			.active{
				font-weight: 700;

			}
		}
		.component-box{	
			padding: 8% 4%;
			box-sizing:border-box;
		}
	}

	.cube-scroll-content{
	    display: inline-block;
   	}
    .list-wrapper{
   		white-space: nowrap;
   		margin-left: 0;
   	}
  	.list-item{
    	display: inline-block;
  	}

</style>